                </div>
                <!-- Content -->
                <div class="modalScrY">
                    <div class="modal-body">
                        <!-- modal-body -->
                        <!-- form -->
                        <ul class="group paB15 clear">
                            <li class="flL w33a marRS">
                                <div class="formCon w100">
                                    <input class="ft1 w100" placeholder="" v-model="Service.MyFormData.BraBlacklistName">
                                </div>
                            </li>
                            <li class="flL w33a marRS">
                                <div class="formCon w100">
                                    <input class="ft1 w100" placeholder=":+886-9123465 | 0932145668" v-model="Service.MyFormData.BraBlacklistTel" p>
                                </div>
                            </li>
                            <li class="formSel w33">
                                <select class="formCon" v-model="Service.MyFormData.BraBlacklistSex">
                                    <option value="0"></option>
                                    <option v-for="(Data,i) in PMSConfig.MyOptionData.MemberSex" :key="i" :value="Data.Val">{{Data.Txt}}</option>
                                </select>
                            </li>
                        </ul>
