import { createRouter, createWebHistory } from 'vue-router';
// 
import AuthService from "@/Services/Auth.service";
// 
import LoginView from "@/views/LoginView.vue";
// (/)
import Consoles from "@/views/Consoles.vue";
//  > 
import Overview from "@/views/Overview/Overview.vue";
//  > 
import Orders from "@/views/Orders/Orders.vue";
import OrdersList from "@/views/Orders/list.vue";
import OrdersDetailStay from "@/views/Orders/detailStay.vue";
import OrdersDetailRest from "@/views/Orders/detailRest.vue";
import OrdersModifyStay from "@/views/Orders/modStay.vue";
import OrdersModifyRest from "@/views/Orders/modRest.vue";
//  > -
import OrdersStay from "@/views/OrdersStay/OrdersStay.vue";
import OrdersStayAdd from "@/views/OrdersStay/add.vue";
//  > -
import OrdersRest from "@/views/OrdersRest/OrdersRest.vue";
import OrdersRestAdd from "@/views/OrdersRest/add.vue";
//  > 
import StoreRoomState from "@/views/StoreRoomState/StoreRoomState.vue";
import StoreRoomStateList from "@/views/StoreRoomState/list.vue";
//  > 
import StoreBlacklist from "@/views/StoreBlacklist/StoreBlacklist.vue";
import StoreBlacklistList from "@/views/StoreBlacklist/list.vue";
//  > 
import RoomsRoomType from "@/views/RoomsRoomType/RoomsRoomType.vue";
import RoomsRoomTypeList from "@/views/RoomsRoomType/list.vue";
//  > 
import Users from "@/views/Users/Users.vue";
import UsersList from "@/views/Users/list.vue";
import UsersPermissions from "@/views/Users/permissions.vue";
// 
import RoomsArea from "@/views/RoomsArea/RoomsArea.vue";
import RoomsAreaList from "@/views/RoomsArea/list.vue";

const routes = [
    {
        // (/)
        name: "Consoles",
        path: "/",
        component: Consoles,
        children: [
            //  > 
            { 
                path: '', 
                component: RoomsAreaList 
            },
            //  > 
            {
                path: 'Orders',
                component: Orders,
                children: [
                    { 
                        path: '', 
                        redirect: to => { return { path: (location.pathname.slice(-1) == "/" ? location.pathname : location.pathname + "/") + 'list' } } 
                    },
                    { 
                        path: 'list', 
                        component: OrdersList
                    },
                    { 
                        path: 'detailStay', 
                        component: OrdersDetailStay
                    },
                    { 
                        path: 'detailRest', 
                        component: OrdersDetailRest
                    },
                    { 
                        path: 'modStay', 
                        component: OrdersModifyStay
                    },
                    { 
                        path: 'modRest', 
                        component: OrdersModifyRest
                    }
                ]
            },
            //  > -
            {
                path: 'OrdersStay',
                component: OrdersStay,
                children: [
                    { 
                        path: '', 
                        redirect: to => { return { path: (location.pathname.slice(-1) == "/" ? location.pathname : location.pathname + "/") + 'add' } } 
                    },
                    { 
                        path: 'add', 
                        component: OrdersStayAdd 
                    }
                ]
            },
            //  > -
            {
                path: 'OrdersRest',
                component: OrdersRest,
                children: [
                    { 
                        path: '', 
                        redirect: to => { return { path: (location.pathname.slice(-1) == "/" ? location.pathname : location.pathname + "/") + 'add' } } 
                    },
                    { 
                        path: 'add', 
                        component: OrdersRestAdd 
                    }
                ]
            },
            //  > 
            {
               path: 'StoreRoomState',
               component: StoreRoomState,
               children: [
                   { 
                       path: '', 
                       redirect: to => { return { path: (location.pathname.slice(-1) == "/" ? location.pathname : location.pathname + "/") + 'list' } } 
                   },
                   { 
                       path: 'list', 
                       component: StoreRoomStateList 
                   }
               ]
           },
            //  > 
            {
                path: 'StoreBlacklist',
                component: StoreBlacklist,
                children: [
                    { 
                        path: '', 
                        redirect: to => { return { path: (location.pathname.slice(-1) == "/" ? location.pathname : location.pathname + "/") + 'list' } } 
                    },
                    { 
                        path: 'list', 
                        component: StoreBlacklistList 
                    }
                ]
            },
            //  > 
            {
                path: "RoomsRoomType",
                component: RoomsRoomType,
                children: [
                    { 
                        path: '', 
                        redirect: to => { return { path: (location.pathname.slice(-1) == "/" ? location.pathname : location.pathname + "/") + 'list' } } 
                    },
                    { 
                        path: 'list', 
                        component: RoomsRoomTypeList 
                    }
                ]
            },
            //  > 
            {
                path: 'Users',
                component: Users,
                children: [
                    { 
                        path: '', 
                        redirect: to => { return { path: (location.pathname.slice(-1) == "/" ? location.pathname : location.pathname + "/") + 'list' } } 
                    },
                    { 
                        path: 'list', 
                        component: UsersList 
                    },
                    { 
                        path: 'permissions', 
                        component: UsersPermissions
                    }
                ]
            },
             // 
             {
                path: 'RoomsArea',
                component: RoomsArea,
                children: [
                    { 
                        path: '', 
                        redirect: to => { return { path: (location.pathname.slice(-1) == "/" ? location.pathname : location.pathname + "/") + 'list' } } 
                    },
                    { 
                        path: 'list', 
                        component: RoomsAreaList 
                    }
                ]
            },
        ]
    }
]

const router = createRouter({
    history: createWebHistory('/pms_clinet/'),
    routes
})

// :
let isAuthenticated = false;
let haveAuth = false;// 
let OldMenuCode = "";
router.beforeEach(async (to, from) => {
    //console.log("To name:" + to.name);
    //console.log("fullPath:" + to.fullPath);
    const StoreNo = to.params.branch || "";
    if (to.fullPath != "/") {
        // :
        const MenuCode = to.fullPath.split('/')[3] || "";

        //console.log("OldItem=" + OldItem + "/ItemCode=" + ItemCode);
        //   MenuCode=Empty 
        if (OldMenuCode != MenuCode || MenuCode.trim() == "")
            haveAuth = true;
        else
            haveAuth = false;
        //AuthService.CurrentItemCode = ItemCode;
        OldMenuCode = MenuCode;// 

        console.log("MenuCode=" + MenuCode + "/StoreNo=" + StoreNo + "/haveAuth=" + haveAuth);

        // 
        if (haveAuth) {
            return 
            try {
                await AuthService.AuthCheck(StoreNo, MenuCode).then(response => {
                    isAuthenticated = response;
                }).catch(fail => {
                    isAuthenticated = fail;
                });
            } catch {
                isAuthenticated = false;
            }
            if (isAuthenticated && AuthService.MyBraUsersAuthMenu.Status == "SUCCESS") {
                return true;
            } else {
                router.push({ path: "/", replace: true });
            }
        } else {
            //   
        }
    }
})

export default router