<template>
    <article class="conWrap">
        <!--==========================  ==========================-->
        <div>
            <section class="h100">
                <div class="infoS" id="SearchBlock">
                    <div class="w10 maR1">
                        <input v-model="Service.SearchName" @change="SearchChange()" class="form-control ft1" type="text" placeholder="">
                    </div>
                    <div class="w10 maR1">
                        <input v-model="Service.SearchTel" @change="SearchChange()" class="form-control ft1" type="text" placeholder="">
                    </div>
                    <div class="w10 maR1 formSel">
                        <select v-model="Service.SearchState" @change="SearchChange()" class="formCon">
                            <option value=""></option>
                            <option v-for="(Data,i) in PMSConfig.MyOptionData.OrdersState" :key="i" :value="Data.Val">{{Data.Txt}}</option>
                        </select>
                    </div>
                    <div class="formSel">
                        <select v-model="Service.SearchDateType" class="formCon radiuTL bdR0">
                            <option value=""></option>
                            <option value="1"></option>
                        </select>
                    </div>
                    <div class="marRS">
                        <input class="form-control cGy radiuRB" type="text" id="SearchDate">
                    </div>
                    <div class="w15 marRS">
                        <input v-model="Service.SearchSerialNo" @change="SearchChange()" class="form-control ft1" type="text" placeholder="">
                    </div>
                    <div class="marRS" data-toggle="modal" data-target="#DialogStoreChannel">
                        <button type="button" class="btn btnSt5 ft1"></button>
                    </div>
                    <!-- button -->
                    <div class="btnOper sty4">
                        <button @click="SearchReset()" type="button" class="btn btnDel2 ft1 maR1 none" data-dismiss="modal" id="SearchReset"></button>
                        <button @click="SearchSubmit()" type="button" class="btn btnSt3 ft1" data-dismiss="modal"></button>
                    </div>
                </div>
                <!--  -->
                <div class="scrY4">
                    <table class="message ft1 bgcTh cGy">
                        <thead>
                            <tr>
                                <th class="w05"></th> 
                                <th class="w08"></th>
                                <th class="w11"></th>
                                <th class="w04"></th>
                                <th class="w07"></th>
                                <th class="w11"></th>
                                <th class="wS2"></th>
                                <th class="wS2"></th>
                                <th class="wS2"></th>
                                <th class="w05"></th> 
                                <th class="w05"></th>
                            </tr>
                        </thead>
                        <tr @click="DetailOpen(ResultData.BraOrdersNo, ResultData.BraOrdersType)" v-for="(ResultData,i) in Service.MyPaginatedData.Result" :key="i" class="point">
                            <td class="bold cDark">{{ResultData.BraOrdersName}}</td>
                            <td>{{ResultData.BraOrdersTel}}</td>
                            <td>{{ResultData.BraOrdersSerialNo}}</td>
                            <td>                  
                                <span :class="StyleOrderState(ResultData.BraOrdersState)">{{ResultData.BraOrdersStateTxt}}</span>
                            </td>
                            <td>
                                <span class="nowrap">{{ResultData.BraOrdersDate}}</span>
                            </td>
                            <td>
                                <span class="nowrap">{{ResultData.BraOrdersCheckin}}</span>
                            </td>
                            <td class="txtR">
                                <div class="nowrap itemC2 ft2">
                                    <span class="maR">NT$</span>
                                    <span class="">{{PMSKit.MoneyFormat(ResultData.BraOrdersReceiveTotal)}}</span>
                                </div> 
                            </td>
                            <td class="txtR">
                                <div class="nowrap itemC2 ft2">
                                    <span class="maR">NT$</span>
                                    <span class="">{{PMSKit.MoneyFormat(ResultData.BraOrdersReceived)}}</span>
                                </div> 
                            </td>
                            <td class="txtR">
                                <div class="nowrap itemC2 ft2">
                                    <span class="maR">NT$</span>
                                    <span class="">{{PMSKit.MoneyFormat(ResultData.BraOrdersReceive)}}</span>
                                </div> 
                            </td>
                            <td>{{ResultData.BraOrdersChannelTxt}}</td>
                            <td>
                                <button @click.stop="ModifyOpen(ResultData.BraOrdersNo, ResultData.BraOrdersType)" type="button" class="btn btnSt5 ft1"></button>
                            </td>
                        </tr>
                    </table>
                </div>
                <!-- pagination -->
                <div class="jqPages sty point" v-if="Service.PageTotalP>1">
                    <div class="padDingL bgc hoverDisable" name="whj_previousPage" v-if="Service.PageIndex>1" @click="PageChange(Service.PageIndex-1);"> </div>
                    <div class="borDer padDing bgc" name="whj_firstPage" v-if="Service.PageIndex>1" @click="PageChange(1);">1</div>
                    <div class="maL" v-if="Service.PageIndex>1"><span class="material-icons">more_horiz</span></div>
                    <template v-for="(li,i) in Service.Pageli" :key="i">
                        <div class="borDer padDing bgc checKed" v-if="Service.PageIndex==li" name="whj_page" data-page="li">{{li}}</div>
                        <div class="borDer padDing bgc hoVer" v-if="Service.PageIndex!=li" name="whj_page" data-page="li" @click="PageChange(li)">{{li}}</div>
                    </template>
                    <div class="maR" v-if="Service.PageIndex<Service.PageTotalP"><span class="material-icons">more_horiz</span></div>
                    <div class="borDer padDing bgc hoVer" name="whj_lastPage" v-if="Service.PageIndex<Service.PageTotalP" @click="PageChange(Service.PageTotalP);">{{Service.PageTotalP}}</div>
                    <div class="padDingR bgc hoVer" name="whj_nextPage" v-if="Service.PageIndex<Service.PageTotalP" @click="PageChange(Service.PageIndex+1);"> </div>
                    <div class="coLor jumPg"></div>
                    <input type="text" class="coLor" name="whj_toPage" v-model="Service.PageJump">
                    <div class="borDerRd padDing bgc hover butPoin" name="whj_confirm" @click="PageJump()"></div>
                </div>
            </section>
        </div><!-- section out -->
    </article>
    <!--=====  =====-->
    <div class="modal fade zId53 modalSty3" id="DialogStoreChannel" tabindex="-1" aria-labelledby="" data-backdrop="static" data-keyboard="false">
        <div class="modal-dialog modal-dialog-centered maxW_modal">
            <div class="modal-content">
                <!-- Content -->
                <div class="modalScrY">
                    <div class="modal-body">
                        <div class="ft2">
                            <ul class="w100 ui-choose">
                                <li v-for="(Data,i) in Service.MyStoreChannelData.Result" :key="i" :value="Data.Val" class="itemC">
                                    <div class="tableCell">
                                        <div class="sel">{{Data.Txt}}</div>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                <!-- Button -->
                <div class="roomModalPos sty">
                    <div class="btnF">
                        <button @click="DialogStoreChannelReset()" type="button" class="btn btnSt1 min6" data-dismiss="modal"></button>
                        <button @click="DialogStoreChannelSubmit()" type="button" class="btn btnSure min6" data-dismiss="modal"></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import AuthService from "@/Services/Auth.service";
    import SysDataService from "@/Services/SysData.service";
    import PMSKitHelper from "@/Helper/PMSKit.helper.js";
    import OrdersService from "@/Services/Orders.service";

    export default {
        name: "OrdersList",
        data() {
            return {
                Auth: AuthService,              // 
                PMSConfig: SysDataService,      // PMS
                PMSKit: PMSKitHelper,           // PMS
                Service: OrdersService,         // 
            }
        },
        methods: {
            // :
            PageChange(Page) {
                if (Page > 0) {
                    this.Service.PageIndex = Page;
                    this.Service.PageJump = Page;
                    this.Service.ListData();
                }
            },
            // :
            PageJump() {
                if (this.Service.PageJump > this.Service.PageTotalP) {
                    alert("");
                    return false;
                }
                this.Service.PageIndex = this.Service.PageJump;
                this.Service.ListData();
            },
            // :
            SearchSubmit() {
                this.Service.ListData();
            },
            // :
            SearchReset() {
                this.Service.SearchName = "";       //
                this.Service.SearchTel = "";        //
                this.Service.SearchState = "";      //
                this.Service.SearchDateType = "";   //
                this.Service.SearchDate = "";       //
                this.Service.SearchSerialNo = "";   //
                this.Service.SearchChannel = "";    //

                $("#SearchBlock #SearchDate").trigger('cancel.daterangepicker');
                $("#SearchBlock #SearchReset").addClass('none');
                $("#DialogStoreChannel .ui-choose li.selected").removeClass('selected');
                this.Service.ListData();
            },
            // :
            SearchChange() {
                if ($("#DialogStoreChannel .ui-choose li.selected").length > 0){
                    this.Service.SearchChannel = $("#DialogStoreChannel .ui-choose li.selected").attr('value');
                }
                if ($("#SearchBlock #SearchDate").val()!=""){
                    this.Service.SearchDate = $("#SearchBlock #SearchDate").val();
                }else{
                    this.Service.SearchDateType = "";
                    this.Service.SearchDate = "";
                }
                $("#SearchBlock #SearchReset").removeClass('none');
            },
            // :
            DetailOpen(no, type){
                // 
                if (this.Auth.MyBraUsersPower.BraPV != 1) {
                    alert("");
                    return false;
                }

                this.$router.push({ path: "/" + this.$route.params.branch + "/consoles/Orders/detail" + [type==1?'Stay':'Rest'], query: { No: no } });
            },
            // :
            ModifyOpen(no, type){
                // 
                if (this.Auth.MyBraUsersPower.BraPM != 1) {
                    alert("");
                    return false;
                }

                this.$router.push({ path: "/" + this.$route.params.branch + "/consoles/Orders/mod" + [type==1?'Stay':'Rest'], query: { No: no } });
            },
            // :
            DialogStoreChannelSubmit(){
                this.SearchChange();
            },
            // :
            DialogStoreChannelReset(){
                $("#DialogStoreChannel .ui-choose li.selected").removeClass('selected');
                if (this.Service.SearchChannel!=""){
                    $("#DialogStoreChannel .ui-choose li[value=" + this.Service.SearchChannel + "]").addClass('selected');
                }
            },
            // :
            StyleOrderState(val) {
                let css = "cState ";

                if (val=="1"){              // -
                    css += "Color01Bg";
                }else if(val=="2"){         // -
                    css += "Color01Bg";
                }else if(val==3){           // -
                    css += "Color02Bg";
                }else if(val=="4"){         // -
                    css += "Color03Bg";
                }else if(val=="5"){         // -
                    css += "Color05Bg";
                }else if(val>=90){          // (90)
                    if (val==91){           // -
                        css += "Color25Bg"; 
                    }else if(val=="95"){    // -
                        css += "Color55Bg"; 
                    }else{
                        css += "Color24Bg"; // -(92)/(93)/(94)/(96)
                    }
                }

                return css;
            }
        },
        // 
        created() {
            // 
            this.PMSConfig.StoreNo = this.$route.params.branch;
            this.Service.StoreNo = this.$route.params.branch;
        },
        // 
        mounted() {
            let vthis = this;

            // 
            if (this.Auth.MyBraUsersPower.BraPV != 1) {
                alert("");
                this.$router.push({ path: "/", replace: true });
            }
            
            // : > 
            this.PMSKit.DatePicker($("#SearchBlock #SearchDate"), 'Single');
            $("#SearchBlock #SearchDate").on('apply.daterangepicker', function(ev, picker) {
                vthis.SearchChange();
            });
            // : > :
            this.PMSKit.OptionSelect($("#DialogStoreChannel .ui-choose"), 'Single', 'YYYY-MM-DD');

            // 
            this.PMSConfig.OptionData();
            // 
            this.Service.StoreChannelData();
            // 
            this.Service.ListData();
        }
    };
</script>
