<template>
  <article class="conWrap dragContainer">
    <section class="h100">
      <div class="addAreaTit ft5 cMain3 addAreaTit_noPt">
        <span class="roomsArea_flexItem roomsArea_rightItem roomsArea_mb">
          <p class="roomsArea_flexItem_title"> x {{ AreaData.area1.length }} </p>
          <p class="roomsArea_flexItem">
            <!--  -->
            <li class="roomsArea_w15">
              <select
                class="roomsArea_Select"
                v-model="sortkey"
                @change="SortChange"
              >
                <option
                  v-for="(sort, i) in sortList"
                  :key="'sortList' + i"
                  :value="sort.code"
                >
                  {{ sort.name }}
                </option>
              </select>
            </li>
            <!--====  ====-->
            <li class="roomsArea_w10 roomSearchLi">
              <input
                type="text"
                class="form-control roomSearchInput"
                placeholder=""
                v-model="searchingRoomNumber"
              />
              <div class="roomSearchSVG" @click="SearchSubmit">
                <img src="../../../public/icon_search.svg" alt="" srcset="">
              </div>
            </li>
            <!--  -->
            <button
              type="button"
              class="btn btnSt5"
              data-toggle="modal"
              data-target="#setAreaRooms"
              @click="ClearSearch"
            >
              
            </button>
            <!--  -->
            <button
              type="button"
              class="btn btnAdd"
              data-dismiss="modal"
              data-toggle="modal"
              data-target="#addArea"
              @click="AddOpen"
            >
            
            </button>
          </p>
        </span>
        <ul id="area1" class="roomsArea_Area">
          <button
            class="roomsArea_Button"
            :class="{ roomsArea_buttonActived: roomItem.isSelected }"
            :id="roomItem.code"
            v-for="(roomItem, roomIndex) in AreaData.area1"
            :key="'AreaData1' + roomIndex"
          >
            <p :id="roomItem.code">{{ roomItem.code }}</p>
            <p :id="roomItem.code" class="roomsArea_ButtonName">
              {{ roomItem.name }}
            </p>
          </button>
        </ul>
      </div>
      <div class="roomsArea_OtherAreaDiv" :style="`height:${otherAreaDiv}`">
        <div
          class="addAreaTit ft5 cMain3 addAreaTit_noPt"
          v-for="(areaItem, areaIndex) in AreaOptionList"
          :key="'AreaOptionList' + areaIndex"
        >
          <div
            :id="areaItem.code"
            class="roomsArea_flexItem roomsArea_rightItem roomsArea_mb"
          >
            <p class="roomsArea_flexItem_title">{{ areaItem.name }} x {{ AreaData[areaItem.code].length }} </p>
            <p class="roomsArea_flexItem">
              <button
                type="button"
                title=""
                class="btn btnDel"
                data-toggle="modal"
                data-target="#AlertConWrap"
                @click="DeleteOpen(areaItem)"
              >
                
              </button>
              <button
                type="button"
                title=""
                class="btn btnSt5"
                data-toggle="modal"
                data-target="#editArea"
                @click="ModifyOpen(areaItem)"
              >
                
              </button>
            </p>
          </div>
          <ul :id="areaItem.code" class="roomsArea_Area">
            <button
              :id="roomItem.code"
              class="roomsArea_Button"
              :class="{ roomsArea_buttonActived: roomItem.isSelected }"
              v-for="(roomItem, roomIndex) in AreaData[areaItem.code]"
              :key="`AreaData${areaItem.code}` + roomIndex"
            >
              <p :id="roomItem.code">{{ roomItem.code }}</p>
              <p :id="roomItem.code" class="roomsArea_ButtonName">
                {{ roomItem.name }}
              </p>
            </button>
          </ul>
        </div>
      </div>
    </section>
  </article>
  <!-- END conWrap -->
  <!-- ====== () Alert  ====== -->
  <div
    class="modal fade"
    id="AlertConWrap"
    tabindex="-1"
    aria-labelledby=""
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content cGy ft2 padBor0">
        <div class="modal-body">
          <div class="BeAlert_box disCon">
            <div class="BeAlert_image warning">
              <span class="material-symbols-outlined"></span>
            </div>
            <div class="BeAlert_title">
              <span v-if="OpenKey =='Delete'">"{{ Modal.AreaName }}"</span>
              <span v-else></span>
              <span class="material-icons">question_mark</span>
            </div>
            <div class="btnSend">
              <button
                type="button"
                data-dismiss="modal"
                class="btn btnSt1 min6 maR"
                @click="AllClose"
              >
                
              </button>
              <button
                type="button"
                class="btn btnSure min6"
                data-dismiss="modal"
                data-toggle="modal"
                @click="AlertConWrapSubmit"
              >
                
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- ====== ()  :  ====== -->
  <div
    class="modal fade zId53"
    id="setAreaRooms"
    tabindex="-1"
    aria-labelledby=""
    aria-modal="true"
    role="dialog"
  >
    <div class="modal-dialog modal-dialog-centered maxW_modal">
      <div class="modal-content roomsArea_setAreaRooms">
        <!-- Tit -->
        <div class="roomsArea_titleDiv">
          
        </div>
        <div class="w100 posHead2 roomsArea_setAreaRoomsPaddingX roomsArea_marginbottom30">
          <div class="itemC roomsArea_setAreaRooms_border">
            <div class="roomsArea_minContent"></div>
            <div class="w100 maR">
              <input
                class="form-control ft3 roomsArea_roomInput"
                type="text"
                placeholder=""
                v-model="Modal.SelectedAreaRooms"
                data-toggle="modal"
                data-dismiss="modal"
                data-target="#setAreaRoomsSelectRooms"
              />
            </div>
            <button type="button" class="btn btnSt1 min6 roomsArea_clearBtn" @click="ClearAreaRooms">
              
            </button>
          </div>
        </div>
        <div class="modalScrY roomsArea_setAreaRoomsPaddingX">
          <div class="modal-body padS5">
            <div class="tabcontent">
              <ul class="multS ui-choose">
                <li class="itemC"
                :class="[
                    { selected: AreaNoPartitioningOption.code == Modal.SelectedAreaCode },
                    { roomsArea_NotAcceptArea: !AreaNoPartitioningOption.isAccepted },
                ]"
                @click.stop="SelectArea(AreaNoPartitioningOption)">
                  <div class="tableCell">
                    <b></b>
                  </div>
                </li>
                <li
                  class="itemC"
                  :class="[
                    { selected: item.code == Modal.SelectedAreaCode },
                    { roomsArea_NotAcceptArea: !item.isAccepted },
                  ]"
                  v-for="(item, index) in AreaOptionList"
                  :key="'setAreaRooms' + index"
                  @click.stop="SelectArea(item)"
                >
                  <div class="tableCell">
                    <b>{{ item.name }}</b>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div class="roomModalPos sty">
          <div class="btnF roomsArea_setAreaRoomsPaddingXY">
            <button
              type="button"
              class="btn btnSt1 min6"
              data-dismiss="modal"
              @click="AllClose"
            >
              
            </button>
            <button
              type="button"
              class="btn btnSure min6"
              data-dismiss="modal"
              data-toggle="modal"
              @click="SelectAreaSubmit"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- ====== ()  :  ====== -->
  <div
    class="modal fade zId53"
    id="setAreaRoomsSelectRooms"
    tabindex="-1"
    aria-labelledby=""
    aria-modal="true"
    role="dialog"
  >
    <div class="modal-dialog modal-dialog-centered maxW_modal">
      <div class="modal-content roomsArea_setAreaRooms">
        <div class="roomsArea_titleDiv">
          
        </div>
        <div class="modalScrY roomsArea_setAreaRoomsPaddingX">
          <div class="modal-body padS5">
            <div class="tabcontent">
              <ul class="multS ui-choose">
                <li
                  class="itemC roomsArea_RoomsLi"
                  :class="{ selected: item.isSelected }"
                  v-for="(item, index) in RoomsOptionList"
                  :key="'setAreaRooms' + index"
                  @click.stop="SelectRooms(item)"
                >
                  <div class="tableCell">
                    <b>{{ item.code }}</b>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div class="roomModalPos sty">
          <div class="btnF roomsArea_setAreaRoomsPaddingXY">
            <button
              type="button"
              class="btn btnSt1 min6"
              data-dismiss="modal"
              @click="AllClose"
            >
              
            </button>
            <button
              type="button"
              class="btn btnSure min6"
              data-dismiss="modal"
              data-toggle="modal"
              data-target="#setAreaRooms"
              @click="SelectRoomsSubmit"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- ====== ()  ====== -->
  <div
    class="modal fade zId52"
    id="editArea"
    tabindex="-1"
    aria-labelledby=""
    aria-hidden="true"
    data-backdrop="static"
    data-keyboard="false"
  >
    <div
      class="modal-dialog modal-dialog-centered maxW_modal roomsArea_ModalDiv"
    >
      <div class="modal-content cGy ft2">
        <div class="w100 posHead2">
          <div class="ft5 cDark topTit bgcL2">
            
          </div>
        </div>
        <div class="modalScrY">
          <ul class="modal-body">
            <li class="paB2">
              <div class="roomsArea_flexItem">
                <p class="roomsArea_ModalInputTitle"></p>
                <input
                  type="text"
                  class="form-control wAuto roomsArea_ModalInput"
                  v-model="Modal.AreaName"
                />
              </div>
            </li>
          </ul>
        </div>
        <div class="roomModalPos sty">
          <div class="btnF">
            <button
              type="button"
              class="btn btnSt1 min6"
              data-dismiss="modal"
              @click="AllClose"
            >
              
            </button>
            <button
              type="button"
              class="btn btnSure min6"
              data-dismiss="modal"
              data-toggle="modal"
              data-target="#AlertConWrap"
              v-if="isTakenName"
            >
              
            </button>
            <button
              type="button"
              class="btn btnSure min6"
              data-dismiss="modal"
              @click="ModifySubmit"
              v-else
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
   <!-- ====== ()  ====== -->
   <div
    class="modal fade zId52"
    id="addArea"
    tabindex="-1"
    aria-labelledby=""
    aria-hidden="true"
    data-backdrop="static"
    data-keyboard="false"
  >
    <div
      class="modal-dialog modal-dialog-centered maxW_modal roomsArea_ModalDiv"
    >
      <div class="modal-content cGy ft2">
        <div class="w100 posHead2">
          <div class="ft5 cDark topTit bgcL2">
            
          </div>
        </div>
        <div class="modalScrY">
          <ul class="modal-body">
            <li class="paB2">
              <div class="roomsArea_flexItem roomsArea_ModalDiv_border">
                <p class="roomsArea_ModalInputTitle"></p>
                <input
                  type="text"
                  class="form-control wAuto roomsArea_ModalInput"
                  placeholder=""
                  v-model="Modal.AreaName"
                />
              </div>
            </li>
          </ul>
        </div>
        <div class="roomModalPos sty">
          <div class="btnF">
            <button
              type="button"
              class="btn btnSt1 min6"
              data-dismiss="modal"
              @click="AllClose"
            >
              
            </button>
            <button
              type="button"
              class="btn btnSure min6"
              data-dismiss="modal"
              data-toggle="modal"
              data-target="#AlertConWrap"
              v-if="isTakenName"
            >
              
            </button>
            <button
              type="button"
              data-dismiss="modal"
              class="btn btnSure min6"
              @click="AddSubmit"
              v-else
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AuthService from '@/Services/Auth.service'

export default {
  name: 'RoomsAreaList',
  data() {
    return {
      Auth: AuthService, // 
      OpenKey: '', //AddModifyDelete
      Modal: {
        AreaCode: '',
        AreaName: '',
        SelectedAreaRooms: '',
        SelectedAreaCode: '',
      },
      sortkey: 'ASC',
      sortList: [
        { code: 'ASC', name: '' },
        { code: 'DESC', name: '' },
      ],
      searchingRoomNumber: '', //""
      AreaNoPartitioningOption: {
        code: 'area1',
        name: '',
        isAccepted: true,
      },
      AreaOptionList: [
        // { code: 'area6', name: '', isAccepted: true },
        // { code: 'area5', name: '', isAccepted: true },
        { code: 'area4', name: '', isAccepted: true },
        { code: 'area3', name: '', isAccepted: true },
        { code: 'area2', name: '()SPA', isAccepted: false },
      ],
      RoomsOptionList: [
        { code: '6201', name: '', isSelected: false },
        { code: '6202', name: '', isSelected: false },
        { code: '6203', name: '', isSelected: false },
        { code: '6204', name: '', isSelected: false },
        { code: '6205', name: '', isSelected: false },
        { code: '6206', name: '', isSelected: false },
      ],
      AreaData: {
        area1: [],
        area6: [],
        area5: [],
        area4: [],
        area3: [],
        area2: [],
      },
    }
  },
  computed: {
    isTakenName:function(){
      let theItem = this.AreaOptionList.find(item=>{
        return item.name.trim() == this.Modal.AreaName.trim()
      })
      return theItem ? true : false
    },
    otherAreaDiv:function(){
      let headDiv = 81 //navbar
      let RoomsAreaPaddingY = 10 * 2 //
      let emptyArea1Height = 106 // 
      let buttonHeight = 83.5 //button
      let gapY = 10 //button y
      let numOfColumn = 7 //

      let area1Length = this.AreaData['area1'].length
      let columns = Math.ceil(area1Length/numOfColumn)
      let totalHeight = columns*buttonHeight+(numOfColumn-1)*gapY+emptyArea1Height+RoomsAreaPaddingY+headDiv
      return `calc(100vh - ${totalHeight}px);`
    },
  },
  watch: {
    // "Modal.RoomType":function(){
    // }
  },
  methods: {
    ListData() {
      // area  rooms
      this.RoomsOptionList = []
      let floor = ['52', '62', '72']
      floor.map((str) => {
        for (let index = 1; index < 5; index++) {
          let num = index < 10 ? `0${index}` : index.toString()
          let roomItem = {
            code: str + num,
            name: '',
            isSelected: false,
          }
          this.RoomsOptionList.push(roomItem)
        }
      })
      this.AreaData['area1'] = JSON.parse(JSON.stringify(this.RoomsOptionList))
    },
    Sorting(areaCode = '') {
      if (!areaCode || !this.AreaData[areaCode]) return
      if (this.sortkey == 'ASC') {
        this.AreaData[areaCode].sort((x, y) => {
          return x.code.localeCompare(y.code)
        })
      }
      if (this.sortkey == 'DESC') {
        this.AreaData[areaCode].sort((x, y) => {
          return y.code.localeCompare(x.code)
        })
      }
    },
    SortChange() {
      //sort
      this.Sorting('area1')
      this.AreaOptionList.map((item) => this.Sorting(item.code))
    },
    ResetModal() {
      this.Modal['AreaCode'] = ''
      this.Modal['AreaName'] = ''
      this.Modal['SelectedAreaRooms'] = []
      this.Modal['SelectedAreaCode'] = []
    },
    //Btoon
    ResetButton() {
      this.RoomsOptionList.map((item) => (item.isSelected = false)) //RoomsOptionList
      // AreaData
      Object.entries(this.AreaData).map((arr) => {
        arr[1].map((item) => (item.isSelected = false))
      })
    },
    // :
    AllClose() {
      setTimeout(()=>{
        this.ResetModal()
        this.ResetButton()
        this.OpenKey = ''
      },300)
    },
    // :
    ClearSearch(){
      this.searchingRoomNumber = ''
      this.SearchSubmit()
    },
    // :
    SearchSubmit() {
      let areaDataEntries = Object.entries(this.AreaData)
      areaDataEntries.map((arr) => {
        arr[1].map((item) =>{
          if(item?.code == this.searchingRoomNumber) item.isSelected = true
          else  item.isSelected = false
        })
      })
    },
    // :
    AddOpen() {
      if (this.Auth.MyBraUsersPower.BraPA != 1) {
        alert('')
        return false
      }
      this.OpenKey = 'Add'
      this.ClearSearch()
    },
    // :
    AddSubmit() {
      if (this.Auth.MyBraUsersPower.BraPA != 1) {
        alert('')
        return false
      }
      if(!this.Modal.AreaName) return this.AllClose()
      let uniqueId = 'id' + Date.now();
      let areaOption = {
        code: uniqueId, 
        name: this.Modal.AreaName, 
        isAccepted: true
      }
      setTimeout(()=>{
        this.AreaOptionList.unshift(areaOption)
        this.AreaData[uniqueId] = []
      },300)
      this.AllClose()
    },
    // :
    ClearAreaRooms() {
      this.Modal.SelectedAreaRooms = ''
      this.ResetButton()
    },
    // :
    SelectArea(areaItem) {
      if (!areaItem?.isAccepted) return
      if (areaItem.code == this.Modal.SelectedAreaCode) {
        this.Modal.SelectedAreaCode = ''
      } else {
        this.Modal.SelectedAreaCode = areaItem.code
      }
    },
    // : 
    SelectAreaSubmit() {
      if (!this.Modal.SelectedAreaCode || !this.Modal.SelectedAreaRooms) return this.AllClose()
      let areaDataEntries = Object.entries(this.AreaData)
      this.RoomsOptionList.map(roomItem=>{
        if(roomItem.isSelected){
          //
          areaDataEntries.find((arr) => {
            let theItemIndex = arr[1].findIndex((item) => item?.code == roomItem.code)
            if (theItemIndex > -1) arr[1].splice(theItemIndex,1)
          })
          //
          roomItem.isSelected = false
          let theCopyRoomItem = JSON.parse(JSON.stringify(roomItem))
          this.AreaData[this.Modal.SelectedAreaCode].push(theCopyRoomItem)
        }
      })
      //sort
      this.Sorting(this.Modal.SelectedAreaCode)
      //
      this.AllClose()
    },
    // :
    SelectRooms(roomItem) {
      roomItem.isSelected = !roomItem.isSelected //RoomsOptionList
      // AreaData
      Object.entries(this.AreaData).find((arr) => {
        let theItem = arr[1].find((item) => item?.code == roomItem.code)
        if (theItem) {
          theItem.isSelected = roomItem.isSelected
        }
      })
    },
    //: 
    SelectRoomsSubmit() {
      let filteredArray = this.RoomsOptionList.filter((item) => item.isSelected)
      let codeArray = filteredArray.map((item) => item.code)
      this.Modal.SelectedAreaRooms = codeArray.join(', ')
    },
    // :
    ModifyOpen(areaItem) {
      if (this.Auth.MyBraUsersPower.BraPM != 1) {
        alert('')
        return false
      }
      if (!areaItem) return
      this.OpenKey = 'Modify'
      this.ClearSearch()
      this.Modal.AreaCode = areaItem?.code || ''
      this.Modal.AreaName = areaItem?.name || ''
    },
    // :
    ModifySubmit() {
      if (this.Auth.MyBraUsersPower.BraPM != 1) {
        alert('')
        return false
      }
      let theTtem = this.AreaOptionList.find(
        (item) => item.code == this.Modal.AreaCode,
      )
      setTimeout(()=>{
        if(theTtem && theTtem.name) theTtem.name = this.Modal.AreaName
      },300)
      this.AllClose()
    },
    // /:
    DeleteOpen(areaItem) {
      if (this.Auth.MyBraUsersPower.BraPD != 1) {
        alert('')
        return false
      }
      if (!areaItem) return
      this.OpenKey = 'Delete'
      this.ClearSearch()
      this.Modal.AreaCode = areaItem?.code || ''
      this.Modal.AreaName = areaItem?.name || ''
    },
    // :
    DeleteSubmit() {
      if (this.Auth.MyBraUsersPower.BraPD != 1) {
        alert('')
        return false
      }
      if (!this.Modal.AreaCode) return this.AllClose()
      this.AreaData['area1'] = [
        ...this.AreaData['area1'],
        ...this.AreaData[this.Modal.AreaCode],
      ]
      //AreaOptionList
      let theIndex = this.AreaOptionList.findIndex(item=>item.code == this.Modal.AreaCode)
      if(theIndex > -1) this.AreaOptionList.splice(theIndex,1)
      //AreaData
      delete this.AreaData[this.Modal.AreaCode]
      this.Sorting('area1')
      this.AllClose()
    },
    // 
    AlertConWrapSubmit(){
      if(this.OpenKey =='Delete') return this.DeleteSubmit()
      else return this.AllClose()
    }
  },
  // 
  mounted() {
    // 
    if (this.Auth.MyBraUsersPower.BraPV != 1) {
      alert(':')
      this.$router.push({ path: '/', replace: true })
    }
    let self = this
    $(
      function () {
        //ul
        var drag = {
          class_name: null, //
          permitDrag: false, //

          _x: 0, //x
          _y: 0, //y
          _left: 0, //
          _top: 0, //

          old_elm: null, //
          tmp_elm: null, //
          new_elm: null, //
          tmp: null,
          buttonId: '',
          //
          init: function (className) {
            //classname(id)
            drag.class_name = className

            //
            $('.' + drag.class_name).on('mousedown', 'ul button', function (
              event,
            ) {
              //
              drag.permitDrag = true
              //
              drag.old_elm = $(this)
              //
              drag.mousedown(event)
              return false
            })

            //
            $(document).mousemove(function (event) {
              //
              if (!drag.permitDrag) return false
              //
              drag.mousemove(event)
              return false
            })

            //
            $(document).mouseup(function (event) {
              //
              if (!drag.permitDrag) return false
              //
              drag.permitDrag = false
              //
              drag.mouseup(event)
              return false
            })
          },
          // 
          mousedown: function (event) {
            self.ClearSearch()
            // console.log('mousedown',event)
            //1.
            drag.tmp_elm = $(drag.old_elm).clone()

            //2.    
            drag._x = $(drag.old_elm).offset().left
            drag._y = $(drag.old_elm).offset().top

            var e = event || window.event
            drag._left = e.pageX - drag._x
            drag._top = e.pageY - drag._y

            //3.
            $(drag.tmp_elm).css({
              position: 'absolute',
              'background-color': 'rgb(209, 85, 28)',
              color: 'white',
              'z-index': '99999',
              left: drag._x, //
              top: drag._y, //
            })

            //4.
            drag.buttonId = event.target.id
            drag.tmp = $(drag.old_elm).parent().append(drag.tmp_elm)
            // drag.tmp_elm = $(drag.tmp).find(drag.tmp_elm)
            $(drag.tmp_elm).css('cursor', 'move')
            let buttonId = drag.buttonId
            let theButtonIdex = null
            let theArr = Object.entries(self.AreaData).find((arr) => {
              theButtonIdex = arr[1].findIndex((item) => item?.code == buttonId)
              return theButtonIdex > -1
            })
            if (theArr) {
              theArr[1][theButtonIdex].isSelected = true
            }
          },
          // 
          mousemove: function (event) {
            // console.log('mousemove')

            //2.
            var e = event || window.event
            var x = e.pageX - drag._left
            var y = e.pageY - drag._top
            var maxL = $(document).width() - $(drag.old_elm).outerWidth()
            var maxT = $(document).height() - $(drag.old_elm).outerHeight()
            //
            x = x < 0 ? 0 : x
            x = x > maxL ? maxL : x
            y = y < 0 ? 0 : y
            y = y > maxT ? maxT : y

            //3.
            $(drag.tmp_elm).css({
              left: x,
              top: y,
            })

            //
            $.each($('.' + drag.class_name + ' ul'), function (index, value) {
              // ()
              var box_x = $(value).offset().left //x
              var box_y = $(value).offset().top //y
              var box_width = $(value).outerWidth() //
              var box_height = $(value).outerHeight() //

              //
              if (
                e.pageX > box_x &&
                e.pageX < box_x - 0 + box_width &&
                e.pageY > box_y &&
                e.pageY < box_y - 0 + box_height
              ) {
                //xy
                if (
                  $(value).offset().left !==
                    drag.old_elm.parent().offset().left ||
                  $(value).offset().top !== drag.old_elm.parent().offset().top
                ) {
                  $(value).css('background-color', '#e3d5a3')
                }
              } else {
                //
                $(value).css('background-color', '#FAFAF3')
              }
            })
          },
          // 
          mouseup: function (event) {
            // console.log('mouseup')
            //
            $(drag.tmp_elm).remove()

            //
            var e = event || window.event

            $.each($('.' + drag.class_name + ' ul'), function (index, value) {
              // ()
              var box_x = $(value).offset().left //x
              var box_y = $(value).offset().top //y
              var box_width = $(value).outerWidth() //
              var box_height = $(value).outerHeight() //

              let buttonId = drag.buttonId
              let theButtonIdex = null
              let buttonAreaId = ''
              let theArr = Object.entries(self.AreaData).find((arr) => {
                theButtonIdex = arr[1].findIndex(
                  (item) => item?.code == buttonId,
                )
                return theButtonIdex > -1
              })
              if (theArr) {
                buttonAreaId = theArr[0]
                self.AreaData[buttonAreaId][theButtonIdex].isSelected = false
              }

              //
              if (
                e.pageX > box_x &&
                e.pageX < box_x - 0 + box_width &&
                e.pageY > box_y &&
                e.pageY < box_y - 0 + box_height
              ) {
                //xy
                if (
                  $(value).offset().left !==
                    drag.old_elm.parent().offset().left ||
                  $(value).offset().top !== drag.old_elm.parent().offset().top
                ) {
                  // //
                  // drag.tmp = $(drag.old_elm).clone()
                  // var newObj = $(value).append(drag.tmp)
                  // $(drag.old_elm).remove()
                  // //
                  // drag.new_elm = $(newObj).find(drag.tmp)
                  if (theArr) {
                    let areaId = value.id
                    let theItem = self.AreaData[buttonAreaId].splice(
                      theButtonIdex,
                      1,
                    )
                    self.AreaData[areaId].push(theItem[0])
                    self.Sorting(areaId)
                  }
                }
              }
              //
              $(value).css('background-color', '#FAFAF3')
              // drag.buttonId = ''
            })
          },
        }
        drag.init('dragContainer')
      }.bind(self),
    )
    // 
    this.ListData()
  },
  beforeDestroy() {},
}
</script>
