import http from "@/Services/HttpAxios";
import HeadersConfig from "@/Services/HeadersConfig";                               // Header
import { BraUsersAuthLogin, BraUsersAuthStore, BraUsersAuthMenu, BraUserLogin } from "@/Models/BraUsers/BraUserAuth.model";// 
import { BraUsersPower } from "@/Models/BraUsers/BraUsersPower.model";

class AuthService {
    MyBraUserLogin = new BraUserLogin();            // 
    MyBraUserAuthLogin = new BraUsersAuthLogin();   // 
    MyBraUsersAuthStore = new BraUsersAuthStore();  // 
    MyBraUsersAuthMenu = new BraUsersAuthMenu();    // 
    MyBraUsersPower = new BraUsersPower();          // 
    CurrentItemCode = "";                           // ItemCode
    CurrentItemId = "";                             // ItemId
    CurrentSotreId = "";                            // 
    
    // 
    LoginService() {
        return http.post(`/OAuth/LoginCheck`, this.MyBraUserLogin);
    }
    // 
    StoreCheck(StoreNo) {
        const Config = HeadersConfig.LoginTokenHeaders();// 
        return http.post(`/OAuth/StoreCheck?StoreNo=${StoreNo}`, null, Config);
    }
    // 
    async AuthCheck(StoreNo,MenuCode) {
        return new Promise((resolve, reject) => {
            const Config = HeadersConfig.TokenHeaders();// 
            //console.log(`/OAuth/MenuCheck?StoreNo=${StoreNo}&MenuCode=${MenuCode}`);
            try {
                http.post(`/OAuth/MenuCheck?StoreNo=${StoreNo}&MenuCode=${MenuCode}`, null, Config).then(response => {
                    //console.log(response.data);
                    //  Model
                    //  import AuthService from "../Services/AuthService";  PowerList 

                    this.MyBraUsersAuthMenu = response.data;
                    console.log(this.MyBraUsersAuthMenu);
                    if (this.MyBraUsersAuthMenu.Status == "SUCCESS") {
                        // 
                        this.MyBraUsersPower = Object.assign({}, this.MyBraUsersAuthMenu.PowerList[0]);
                        resolve(true);
                    } else {
                        reject(false);
                    }
                }).catch(() => {
                    reject(false);
                });
            } catch {
                reject(false);
            }
        });
    }
}

export default new AuthService();